class Api {
    constructor(options) {
        this._options = options;
    }

    getInitialCards() {
        return fetch('https://mesto.nomoreparties.co/v1/cohort-43/cards', {
            headers: {
                authorization: 'ffa77eee-e2e0-4984-80c5-a3aae1c9cc92'
            }
        })
            .then(res => {
                if (res.ok) {
                    return res.json();
                }
                return Promise.reject(`Ошибка: ${res.status}`);
            });
    }

    getProfile() {
        return fetch('https://mesto.nomoreparties.co/v1/cohort-43/cards', {
            method: 'GET',
            headers: {
                authorization: 'ffa77eee-e2e0-4984-80c5-a3aae1c9cc92'
            }
        })
            .then(res => {
                if (res.ok) {
                    return res.json();
                }
                return Promise.reject(`Ошибка: ${res.status}`);
            });
    }
}



